body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  box-sizing: border-box;
  font-size: 20px;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}
.Game {
    text-align: center;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.Game .wrapper {
    padding: 10px;
    background-color: #ccc;
    margin-bottom: 10px;
}
.Game .status {
    display: inline-block;
    margin: 0 auto;
}
.Game .status::after {
    clear: both;
    content: "";
    display: block;
}
.Game .lcd {
    display: inline-block;
    margin: 0 20px;
    font-family: 'Orbitron', monospace, sans-serif;
    color: red;
    background: #333333;
    font-size: 36px;
    float: left;
    width: 100px;
    height: 50px;
    line-height: 56px;
}
.Game .restart {
    text-align: center;
    background: white;
    font-size: 26px;
    margin: 0 20px;
    float: left;
    width: 50px;
    height: 50px;
    line-height: 48px;
}

.Game .bestScore {
    margin-top: 10px;
}

.Square {
    width: 5vh;
    height: 5vh;
    font-size: 4vh;
    font-weight: bold;
    line-height: 4vh;
    background: #eee;
    border: 1px outset #ddd;
    cursor: default;
    display: inline-block;
    float: left;
    outline: none;
}
.Square.unrevealed:active {
    background: #999;
    border: 1px inset #fff;
}
.Square.mine {
    background-color: darkred;
    color: white;
}
.Square.revealed {
    background: #ddd;
}
.Square.number1 {
    color: blue;
}
.Square.number2 {
    color: green;
}
.Square.number3 {
    color: red;
}
.Square.number4 {
    color: darkblue;
}
.Square.number5 {
    color: darkred;
}
.Square.number6 {
    color: teal;
}
.Square.number7 {
    color: #333;
}
.Square.number8 {
    color: deeppink;
}
