.Square {
    width: 5vh;
    height: 5vh;
    font-size: 4vh;
    font-weight: bold;
    line-height: 4vh;
    background: #eee;
    border: 1px outset #ddd;
    cursor: default;
    display: inline-block;
    float: left;
    outline: none;
}
.Square.unrevealed:active {
    background: #999;
    border: 1px inset #fff;
}
.Square.mine {
    background-color: darkred;
    color: white;
}
.Square.revealed {
    background: #ddd;
}
.Square.number1 {
    color: blue;
}
.Square.number2 {
    color: green;
}
.Square.number3 {
    color: red;
}
.Square.number4 {
    color: darkblue;
}
.Square.number5 {
    color: darkred;
}
.Square.number6 {
    color: teal;
}
.Square.number7 {
    color: #333;
}
.Square.number8 {
    color: deeppink;
}
